.course-goals{
  text-align: center;
}

.goal-list {
  list-style: none;
  margin: 2rem 0;
  padding: 0;
}

.goal-list li{
  margin: 1rem 0;
  border: 1px solid peachpuff;
  padding: 1rem;
}

.goal-list li:hover {
  color: brown;
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1)
 
}

.new-goal{
  margin: 2rem 0;
  text-align: center;
  
}

.new-goal button{
  display: inline-block;
  
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}